<template>
  <el-row>
    <el-col :span="24">
      <el-row type="flex" class="row-bg" justify="space-between">
        <el-col :span="24">
          <el-row type="flex" class="row-bg" justify="space-between">
            <el-col :span="12">
              <h3>类型搜索</h3>
            </el-col>
            <el-col :span="12" class="text-align-right">
      
            </el-col>
          </el-row>
        </el-col>
      </el-row>
      <div class="margin-4x"></div>
      
      <el-row class="text-align-right">
        <div class="domain-search-input">
          <el-input placeholder="请输入名称进行搜索" prefix-icon="el-icon-search" v-model="domainSearch"></el-input>
        </div>
        <el-button type="primary" @click="onClickCreateDomain()">添加关键词</el-button>
      </el-row>
      <el-divider />
      <el-row>
        <el-col :span="24" class="search-col">
          <div class="search-form">
            <div class="search-item">
              <div class="item-label">关键词</div>
              <div class="item-content">
                <el-select placeholder="请选择" v-model="key">
                  <el-option v-for="item in keyList" :key="item" :label="item" :value="item" />
                </el-select>
              </div>
            </div>
            <div class="search-item">
              <div class="item-label">HQ</div>
              <div class="item-content">
                <el-select placeholder="请选择" v-model="hq">
                  <el-option  key="false" label="NQ" value="false" />
                  <el-option key="true" label="HQ" value="true" />
                </el-select>
              </div>
            </div>
          </div>
          <div class="button-form">
            <el-button @click="searchItem(tabsBoxName)" type="primary" class="button-new">查询</el-button>
          </div>
        </el-col>
      </el-row>
      <div class="demo-block">
        <el-table :data="
        tableData.filter(
          (data) =>
            !domainSearch ||
            data.domain.toLowerCase().includes(domainSearch.toLowerCase())
        )" 
          :default-sort="{ prop: 'price', order: 'descending' }"
          style="width: 100%">
          <el-table-column prop="id" label="id"></el-table-column>
          <el-table-column prop="name" label="name"></el-table-column>
          <el-table-column prop="price" label="price"></el-table-column>
        </el-table>
      </div>
    </el-col>

    <el-dialog :title="domainTitle" v-model="dialogDomainFormVisible" width="520px" :close-on-click-modal="false">
      <el-form class="form-tag-dialog" size="mini" label-position="left" label-width="130px">
        <el-form-item label="关键词" key="1">
          <el-input v-model="keyStr" :rows="4" type="textarea" placeholder="请输入关键词，并用中文逗号，分割" />
        </el-form-item>
      </el-form>
      <template #footer class="dialog-footer">
        <el-button @click="dialogDomainFormVisible = false">取消</el-button>
        <el-button type="primary" @click="onClickDomainSubmit()" :loading="loading">确定</el-button>
      </template>
    </el-dialog>
  </el-row>
</template>
<script>
import { validatePort, validateDomain, mixin, JXAjax } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      domainTitle: "编辑",
      domainType: "new",
      domainSearch: "",
      dialogDomainFormVisible: false,
      loading: false,
      tableData: [],
      key: "",
      hq:"true",
      keyList: "",
      keyStr:"条约，古典风格",
    };
  },
  
  mounted() {
    var _key_str = localStorage.getItem("ff14_key_list");
    if (_key_str && _key_str.indexOf("，") > -1) { 
      this.keyStr = _key_str
    }
    this.keyList = this.keyStr.split("，")
  },
  methods: {
    searchItem() {
      var t = this;
      JXAjax(
        "post",
        "/ff_key_search",
        {key: t.key,hq:t.hq},
        function (response) {
          t.tableData = response.data.message;
        },
        function () {

        }
      );
    },


    onClickDomainSubmit() {
      var t = this;
      localStorage.setItem("ff14_key_list", t.keyStr);
      t.keyList = t.keyStr.split("，");
      this.dialogDomainFormVisible = false;
    },
    

    onClickCreateDomain() {
      var t = this;
      t.domainTitle = "编辑";
      t.domainType = "new";
      t.dialogDomainFormVisible = true;
    },
  },
};
</script>
<style>
.el-button.button-block {
  display: block;
  margin-left: 0px;
  text-align: right;
  width: 100%;
}
.icon-success {
  color: #67c23a;
  margin-right: 5px;
}
.icon-error {
  color: #f56c6c;
  margin-right: 5px;
}
.icon-warning {
  color: #e6a23c;
  margin-right: 5px;
}
.domain-setting {
  float: right;
}
.domain-redirect {
  margin-top: 5px;
  margin-bottom: 0px !important;
}

.el-form-item.is-required .redirect-box .el-form-item__label:before {
  content: "";
}
.domain-search-input {
  display: block;
  float: left;
  text-align: left;
}
.domain-search-input .el-input {
  width: 100%;
}
.no-padding {
  padding: 0;
}
.node-detail span {
  font-size: 12px;
}
.domain-tabs {
  margin-bottom: 18px;
}

.search-form {
  display: inline-block;
}

.button-form {
  display: inline-block;
}

.button-form button {
  display: inline-flex;
  vertical-align: middle;
  margin-bottom: 18px;
}

.search-form .search-item {
  display: inline-flex;
  vertical-align: middle;
  margin-right: 32px;
  margin-bottom: 18px;
  font-size: 14px;
}

.search-form .item-label {
  display: inline-flex;
  justify-content: flex-end;
  align-items: flex-start;
  flex: 0 0 auto;
  color: #606266;
  height: 40px;
  line-height: 40px;
  padding: 0 12px 0 0;
  box-sizing: border-box;
}

.search-btn {
  display: inline-flex;
  vertical-align: middle;
  margin-right: 10px;
  margin-bottom: 18px;
  font-size: 14px;
}

.search-message {
  background-color: #f0f9eb;
  color: #67c23a;
  padding: 8px 16px;
  line-height: 1.5;
  font-size: 12px;
}

.search-message p {
  padding: 0 8px;
}

.search-form-long {
  width: calc(100% - 238px);
}

.search-item-long {
  width: calc(100% - 30px);
}

.search-item-long .item-content {
  width: 100%;
}
</style>
